.keyword-list {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
  overflow: visible;
}

.keyword-list h3 {
  font-size: 1.1em;
  color: #4A5568;
  margin-bottom: 10px;
  font-weight: 700;
}

/* 자동 생성된 키워드를 담는 컨테이너 */
.keyword-container {
  display: flex;
  flex-wrap: wrap; /* 키워드가 줄 바꿈되도록 설정 */
  gap: 8px; /* 키워드 간 간격 */
}

/* 자동 생성된 키워드 스타일 */
.keyword {
  background-color: #EDF2F7;
  color: #2D3748;
  padding: 8px 12px;
  border-radius: 8px;
  font-size: 0.9em;
  font-weight: 500;
  display: inline-block;
  transition: background-color 0.3s ease, transform 0.2s ease;
  cursor: pointer;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  white-space: nowrap; /* 키워드가 한 줄에 유지되도록 */
}

.keyword:hover {
  background-color: #E2E8F0;
  transform: translateY(-2px);
}

/* 직접 입력할 키워드 스타일 */
.keyword-input {
  padding: 10px;
  margin: 8px 0;
  border: 1px solid #CBD5E0;
  border-radius: 8px;
  width: 95%;
  font-size: 0.9em;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.keyword-input:focus {
  border-color: #5A67D8;
  outline: none;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
}
