.brand-list {
    background-color: #ffffff;
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  .brand-list h3 {
    font-size: 1.1em;
    color: #4A5568;
    margin-bottom: 10px;
    font-weight: 700;
  }
  
  /* 브랜드 키워드를 표시할 컨테이너 */
  .brand-container {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }
  
  .brand-item {
    background-color: #EDF2F7;
    color: #2D3748;
    padding: 8px 12px;
    border-radius: 8px;
    font-size: 0.9em;
    font-weight: 500;
    display: inline-block;
    white-space: nowrap;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  }
  
  .brand-item .remove-button {
    background: none;
    border: none;
    color: #E53E3E;
    font-weight: bold;
    margin-left: 8px;
    cursor: pointer;
  }
  
  .brand-item .remove-button:hover {
    color: #C53030;
  }
  
  .brand-input-container {
    display: flex;
    gap: 8px;
    margin-top: 12px;
  }
  
  .brand-input {
    padding: 10px;
    margin: 0;
    border: 1px solid #CBD5E0;
    border-radius: 8px;
    width: 80%;
    font-size: 0.9em;
  }
  
  .add-button {
    padding: 10px 15px;
    background-color: #5A67D8;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 0.9em;
    cursor: pointer;
  }
  
  .add-button:hover {
    background-color: #4C51BF;
  }
  
  .brand-input:focus {
    border-color: #5A67D8;
    outline: none;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  }