/* 전체 에디터 컨테이너 */
.editor-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 100%;
}

/* 메인 이미지 표시 영역 */
.image-display {
  width: 100%;
  max-width: 600px;
  height: 400px;
  background-color: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
}

.main-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-placeholder {
  color: #888;
  font-size: 1.2rem;
  font-weight: 600;
}

/* 버튼 그룹 스타일 */
.button-group {
  display: flex;
  gap: 15px;
}

.download-button,
.send-button {
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.download-button {
  background-color: #5A67D8;
}

.download-button:hover {
  background-color: #4C51BF;
}

.send-button {
  background-color: #48BB78;
}

.send-button:hover {
  background-color: #38A169;
}

/* 모달 스타일 */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); /* 어두운 배경 */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* 다른 요소 위에 표시되도록 */
}

.modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
}

.phone-input {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.confirm-button,
.cancel-button {
  flex: 1;
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  color: white;
}

.confirm-button {
  background-color: #48BB78;
}

.confirm-button:hover {
  background-color: #38A169;
}

.cancel-button {
  background-color: #f56565;
}

.cancel-button:hover {
  background-color: #e53e3e;
}