.image-options {
  margin-bottom: 20px;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.image-options h3 {
  margin-bottom: 15px;
  color: #333;
  font-size: 1.2em;
  font-weight: 600;
}

.style-dropdown {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f8f9fa;
  color: #333;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.style-dropdown:focus {
  border-color: #5A67D8; /* 포커스 시 보라색 테두리 */
  outline: none;
  box-shadow: 0px 4px 8px rgba(90, 103, 216, 0.2); /* 보라색 그림자 */
}

.style-dropdown option {
  color: #333;
  font-size: 16px;
  padding: 10px;
}
