/* 전체 컨테이너 스타일 */
.preview-section {
  padding: 20px;
  text-align: center;
}

.image-preview {
  display: flex; /* 플렉스 박스로 정렬 */
  justify-content: center; /* 중앙 정렬 */
  gap: 15px; /* 이미지 간 간격 */
  margin-top: 20px;
}

/* 이미지 박스 스타일 */
.image-box {
  width: 150px; /* 고정된 박스 너비 */
  height: 150px; /* 고정된 박스 높이 */
  overflow: hidden; /* 이미지가 박스를 벗어나지 않도록 */
  border-radius: 8px; /* 박스 모서리 둥글게 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 약간의 그림자 효과 */
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

/* 이미지 박스 호버 효과 */
.image-box:hover {
  transform: translateY(-5px); /* 살짝 올라가는 효과 */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15); /* 그림자 강조 */
}

/* 이미지 스타일 */
.preview-image {
  width: 100%; /* 박스 크기에 맞게 이미지 크기 조정 */
  height: 100%; /* 박스 크기에 맞게 이미지 크기 조정 */
  object-fit: cover; /* 이미지가 찌그러지지 않도록 유지 */
}
