.header {
    display: flex;
    justify-content: center; /* 가운데 정렬 */
    align-items: center;     /* 세로로도 중앙 정렬 */
    background-color: #4b0082; /* 보라색 배경 */
    color: white;
    padding: 20px;
    border-radius: 8px;
    width: 10%; /* 전체 너비 차지 */
  }
  
  .header h1 {
    margin: 0;
    font-size: 2rem;
    font-weight: bold;
  }
  