/* 전체 페이지 레이아웃 설정 */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
}

/* 전체 레이아웃 */
.container {
  display: flex;
  justify-content: center;
  padding: 20px;
  gap: 20px;
  box-sizing: border-box;
  flex: 1;
}

/* 옵션 섹션 스타일 */
.options {
  width: 30%;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}

/* 섹션 제목 스타일 */
.options h3 {
  font-size: 1.2em;
  color: #333;
  margin-bottom: 15px;
  font-weight: 600;
}

/* 메시지 입력 필드 스타일 */
.message-input {
  width: 93%;
  padding: 15px;
  font-size: 16px;
  color: #333;
  background-color: #f0f2f5;
  border: none;
  border-radius: 12px; /* 둥근 모서리 */
  box-shadow: inset 0px 1px 4px rgba(0, 0, 0, 0.1); /* 내부 그림자 */
  outline: none;
  transition: box-shadow 0.3s ease, background-color 0.3s ease;
  resize: vertical; /* 수직 크기 조절 가능 */
}

.message-input::placeholder {
  color: #888; /* 플레이스홀더 색상 */
}

.message-input:focus {
  background-color: #ffffff; /* 포커스 시 배경색 변경 */
  box-shadow: 0px 0px 8px rgba(90, 103, 216, 0.2), inset 0px 1px 4px rgba(0, 0, 0, 0.1); /* 포커스 시 외부 그림자 추가 */
  border: 1px solid #5A67D8; /* 포커스 시 보라색 테두리 */
}

/* 버튼 스타일 */
.generate-button {
  width: 100%;
  padding: 12px;
  background-color: #5A67D8;
  color: white;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 20px;
}

.generate-button:hover {
  background-color: #4C51BF;
  transform: translateY(-2px);
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.generate-button:active {
  transform: translateY(0);
}

/* 이미지 섹션 스타일 */
.image-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 65%;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  gap: 20px;
}
